import React from 'react';
import ReactDOMServer from 'react-dom/server';

import dayjs from "dayjs";

import MarkdownParser from "../external/markdown-parser";

// 移除了antd相关的代码，不使用css-modules
const ArticleRead = (props) => {
  const { title, author, cover, excerpt, markdown, postedAt } = props;

  const articleInfo = { author, postedAt };

  return (
    <>
      <div
        className={'readPageBackground'}
      >
        <div className={'readPageContainer'}>
          <Title title={title}/>
          <ArticleInfo {...articleInfo}/>
          <Excerpt excerpt={excerpt}/>
          <HeaderPhoto cover={cover} />
          <Content markdown={markdown}/>
        </div>
      </div>
    </>

  )
};

const Title = ({ title }) => (
  <h1 className={'title'}>
    {title}
  </h1>
);

const HeaderPhoto = ({ cover }) => {
  const { url, ...coverInfo } = cover;

  return (
    <div>
      {
        url
          ? (
            <div>
              <img className={'coverImage'} src={url} alt="cover"/>
              <IsPhotoFromUnsplash coverInfo={coverInfo}/>
            </div>
          ) : (
            null
          )
      }
    </div>
  );
};

const IsPhotoFromUnsplash = ({ coverInfo }) => {
  const { authorName, authorLink } = coverInfo;

  return (
    <>
      {
        authorName
          ? (
            <div className={'coverInfo'}>
              Photo by &nbsp;
              <a
                className={'coverInfoLink'}
                href={authorLink + '?utm_source=write-down&utm_medium=referral'}
                target="_blank"
                rel="noopener noreferrer"
              >
                {authorName}
              </a>
              &nbsp; on &nbsp;
              <a
                className={'coverInfoLink'}
                href={'https://unsplash.com?utm_source=write-down&utm_medium=referral'}
                target="_blank"
                rel="noopener noreferrer"
              >
                unsplash
              </a>
            </div>
          ) : (
            null
          )
      }
    </>
  );
};

const ArticleInfo = ({ author, postedAt }) => (
  <div className={'infoWrapper'}>
    <div>
      Posted by <span className={'author'}>{author}</span>
    </div>
    <div>
      <span>
        {dayjs(postedAt).format('MMM. D, YYYY')}
      </span>
    </div>
  </div>
);

const Excerpt = ({ excerpt }) => (
  <p className={'excerptContainer'}>
    <span className={'excerpt'}>
      {excerpt}
    </span>
  </p>
);

const Content = ({ markdown }) => {
  const htmlOutput = MarkdownParser.render(markdown);

  const renderResult = {
    __html: htmlOutput
  };

  return (
    <div className={'content'}>
      <div
        className={'markdown-body'}
        dangerouslySetInnerHTML={renderResult}
      />
    </div>
  );
};

const articleBody = (data) => ReactDOMServer.renderToStaticMarkup(ArticleRead(data)).toString();

const basicStyle = `
@import url('https://fonts.googleapis.com/css?family=Lora:700|Open+Sans');

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "tnum";
  font-variant: tabular-nums;
  line-height: 1.5;
  color: rgba(0, 0, 0, .65)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3, h4, h5, h6,
h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
  font-family: 'Lora', serif;
}

p, ul, li, a, table, button {
  font-family: 'Open Sans', sans-serif;
}

@font-face {
  font-family: medium-content-serif-font;
  font-style: normal;
  font-weight: 400;
  src: url(data:font/opentype;base64,)
}

:focus {
  outline: none;
}
`;

const articleStyle = `
/* Custom scrollbar style */

::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: #ddd;
}

/* Article style */

.readPageBackground {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0 100px 0;
}

.readPageContainer {
  font-size: 18px;
  width: 700px;
  background-color: #fff;
}

.title {
  font-family: 'Lora', serif;
}

.infoWrapper {
  display: flex;
  justify-content: space-between;
  margin: -12px 0 10px;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  font-weight: 600;
}

.author {
  color: salmon;
}

.excerptContainer {
  padding: 10px;
  border-radius: 10px;
  border: 3px solid #eee;
}

.excerpt {
  color: rgba(0, 0, 0, 0.65);
  font-family: 'medium-content-serif-font', serif;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: .02em;
}

.coverImage {
  display: block;
  max-width: 100%;
}

.coverInfo {
  text-align: center;
  margin: 4px 0 0;
  font-size: 16px;
}

.coverInfoLink {
  text-transform: capitalize;
  color: #000;
  text-decoration: underline;
  font-family: 'Open Sans', sans-serif;
  transition: all 0.3s ease;
}

.coverInfoLink:hover {
  color: gray;
  text-decoration: none;
}

.content {
  margin: 18px 0 0;
}

.content .markdown-body h1,
.content .markdown-body h2,
.content .markdown-body h3,
.content .markdown-body h4,
.content .markdown-body h5,
.content .markdown-body h6 {
  color: #000;
  font-family: 'Open Sans', sans-serif;
}

.content .markdown-body p,
.content .markdown-body ul,
.content .markdown-body li,
.content .markdown-body a,
.content .markdown-body table {
  /*color: rgba(0, 0, 0, 0.8);*/
  line-height: 1.58;
  font-family: 'medium-content-serif-font', serif;
  font-size: 21px;
  letter-spacing: -.003em;
}

.content a {
  color: #000;
  /*text-decoration: underline;*/
  border-bottom: 1px solid #ccc;
  transition: all 0.1s ease;
}

.content a:hover {
  color: gray;
  text-decoration: none;
  border-bottom: none;
}
`;

/**
 * generateStyledHTML() 生成带有样式的 html 文件字符串，供 exportFile() 使用
 *
 * @param {Object} data  生成 html 文件的数据，跟 <ArticleReadPage/> 需要的数据几乎相同
 *
 * @return {String} html 文件的字符串
 */

const generateStyledHTML = (data) =>
`
  <html>
    <header>
      <title>${data.title}</title>
      <meta name="viewport" content="width=device-width, initial-scale=1">
      <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/github-markdown-css/2.10.0/github-markdown.min.css">
      <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/prism-themes@1.0.1/themes/prism-darcula.css">
      <style>
        .emoji {
          height: 1.2em;
        }  
        
        /*
        some optimization to the code block.
        */
        
        .token.keyword {
          font-weight: 900;
        }
        
        .token.inserted {
          color: #50fa7b;
          background: transparent;
        }

        .token.deleted {
          color: #ff5555;
          background: transparent;
        }
        
        abbr[title], abbr[data-original-title] {
          text-decoration: underline dotted;
          cursor: help;
        }

      </style>
      <style>${basicStyle}</style>
      <style>${articleStyle}</style>
    </header>
    <body>
      ${articleBody(data)}
    </body>
  </html>  
`;

export default generateStyledHTML;